.tags {
	text-align: center;
	padding: 20px 0px;
	background-color: #2a2a2a;
}

.tag {
	outline: none;
	border: none;
	color: #eee;
	margin: 0px 10px;
	background-color: transparent;
	cursor: pointer;
}

.active {
	font-weight: bold;
	border-bottom: 1px solid #eee;
}
.container {
	width: 80%;
	margin: auto;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 10px;
}

.image-card {
	
	padding: 5px;
	margin: 5px;
	border: 1px solid #ddd;
	border-radius: 2px;
}

.image {
	width: 100%;
}
@media screen and (max-width: 820px) {
	.image {
		width: 100%;
	}
	.container {
		width: 85%;
		margin: auto;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 7px;
	}
	.image-card {
		align-items: center;
		padding: 5px;
		margin: 2px;
		border: 1px solid #ddd;
		border-radius: 2px;
	}
}
